.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;
  grid-template-areas: "h h" "n c";
  
  grid-template-rows: 50px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
  }
  
  .app-wrapper-content {
  grid-area: c;
  background-color: rgb(237, 234, 238);
  box-shadow: 0 0 10px rgba(43, 41, 41, 0.5);

  }

  
  
  

  
  
  
  
  
 